.polygon-container {
  width: max-content;
  .polygon-rotate {
    height: 80px;
  }
}

#marquee-scroller {
    overflow: hidden;
    white-space: nowrap;
    position: relative;
  }
  .find-anywhere-scrolling-container {
    margin: 5px;
    overflow: hidden;
    white-space: nowrap;

    .marquee-binding-hr {
      height: 1px;
      background: #000;
    }
  }
  .find-anywhere-scrolling {
    font-size: 30px;
    text-transform: uppercase;
    letter-spacing: 3px;
    // animation: marquee 4800s linear infinite;
    // display: inline-block;
  
    img {
      height: 120px;
    }
  }
  @keyframes marquee {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-100%);
    }
  }
  @media screen and (max-width: 400px) {
    .find-anywhere-scrolling {
      font-size: 15px;
  
      img {
        height: 60px;
      }
    }
  }
  @media screen and (min-width: 400px) and (max-width: 550px) {
    .find-anywhere-scrolling {
      font-size: 25px;
  
      img {
        height: 70px;
      }
    }
  }