.gradient {
    padding-bottom: 50px;

    .vintage {
        z-index: 100;

        img {
            max-width: 90vw;
        }
    }

    .gradient-row {
        display: inline-flex;
    }

    .gradient-box {
        width: 20vw;
        height: 20vw;
        margin: 0 1.5vw;
    }

    #box00 {
        background: #3c2d16;
    }

    #box01 {
        background: #4c391c;
    }

    #box02 {
        background: #695023;
    }

    #box03 {
        background: #826139;
    }

    #box10 {
        background: #3e2f19db;
    }

    #box11 {
        background: #4b3a1bd9;
    }

    #box12 {
        background: #695024d9;
    }

    #box13 {
        background: #816139db;
    }

    #box20 {
        background: #5a4d39c2;
    }

    #box21 {
        background: #67583eba;
    }

    #box22 {
        background: #806b46cf;
    }

    #box23 {
        background: #947755d1;
    }
}