.about-header {
    width: 20em;
    max-width: 100%;
}

.about-text {
    font-family: 'Nunito', zxcv;
    font-weight: 500;
    line-height: 1.5em;
}

.about-detail {
    padding-top: 20px;
}

.about-header-img {
    width: 40vw;
}

@media screen and (max-width: 400px) {
    .about-header-img {
        width: 90vw;
    }

    .about-text {
        font-size: 1em;
    }
}

@media screen and (min-width: 400px) and (max-width: 550px) {
    .about-header-img {
        width: 60vw;
    }
}