@tailwind base;
@tailwind components;
@tailwind utilities;
@import './../node_modules/locomotive-scroll/dist/locomotive-scroll';

@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,300;0,6..12,400;0,6..12,700;0,6..12,800;1,6..12,400;1,6..12,600;1,6..12,800&family=Nunito:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,400;0,500;0,600;1,600;0,700;1,700&family=Nunito+Sans:ital,opsz,wght@0,6..12,300;0,6..12,400;0,6..12,700;0,6..12,800;1,6..12,400;1,6..12,600;1,6..12,800&family=Nunito:wght@300&display=swap');

@font-face {
  font-family: zxcv;
  src: url(assets/fonts/zxcv-Light.woff2)
}

@font-face {
  font-family: zxcv;
  font-style: italic;
  src: url(assets/fonts/zxcv-LightItalic.woff2)
}

@font-face {
  font-family: vnwf-cr;
  src: url(assets/fonts/NeueWorld-CondensedRegular.otf)
}
@font-face {
  font-family: vnwf-et;
  src: url(assets/fonts/NeueWorld-ExtendedThin.otf)
}
@font-face {
  font-family: vnwf-scu;
  src: url(assets/fonts/NeueWorld-SemiCondensedUltrabold.otf)
}
@font-face {
  font-family: vnwf-seb;
  src: url(assets/fonts/NeueWorld-SemiExtendedBlack.otf)
}
@font-face {
  font-family: vnwf-scl;
  src: url(assets/fonts/NeueWorld-SuperCondensedLight.otf)
}

@font-face {
  font-family: bc;
  src: url(assets/fonts/bc-med.ttf)
}

/* ------ */

html, body {
  font-family: 'zxcv', serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url(assets/img/circleGradient.png), auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-link {
  font-family: 'Cormorant Garamond', zxcv;
  font-weight: 600;
  /* color: #803783; */
  text-decoration-line: underline;
  /* text-decoration-style: wavy; */
  padding: 0 0.1em;
  text-underline-offset: 0.3em;
}

.text-link:hover {
  color: #76860a;
  /* text-decoration-line: underline; */
  text-decoration-style: wavy;
  text-underline-offset: 0.3em;
}

a[href] {
  cursor: url(assets/img/rightArrow.png), auto;
}

.talk-cursor {
  cursor: url(assets/img/studioMicrophone.png), auto;
}

.newspaper-cursor {
  cursor: url(assets/img/newspaper.png), auto;
}