.press-header,
.talks-header {
  font-family: 'vnwf-et', serif;
  padding: 0;
  width: 100vw;
  font-size: 18vw;
  display: inline-flex;
  justify-content: center;

  div {
    width: 1.2em;
    height: 1.2em;
    line-height: 1.45em;
    text-align: center;
  }
}
.press-1 {
  font-family: 'vnwf-scu', serif;
  background: #000;
  color: #fff;
}
.press-2 {
