.hundred {
    z-index: -1;
    background-image: radial-gradient(
      rgba(255, 202, 53, 0.74) 4%,
      rgb(228, 205, 39) 20%,
      white 60%
    );
  
    .planet {
      font-size: 32vw;
      align-items: center;
      line-height: 63vw;
    }
    .vintage-objects {
      top: 30%;
      height: calc(100vh - 2em) !important;
      width: 100vw;

    }
  }
  .space-holder {
    position: relative;
    width: 100%;
  }
  .sticky {
    position: sticky;
    top: 0;
    height: 100vh;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
  
    .horizotal {
      position: absolute;
      will-change: transform;
  
      .cards {
        position: relative;
        height: 100%;
        padding: 0 0 0 150px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
      }
    }
  }
  .honors-header {
    font-size: 6vw;
    font-family: 'vnwf-scu', serif;
    color: rgb(223, 169, 20);
    padding: 1em;
    overflow-x: scroll;
  }
  .honors-wrapper {
    .honor-card-wrapper {
      display: inline-flex;
      min-width: 100vw;
      justify-content: center;
      bottom: 100px;
      top: auto;
    }
  }
  @media screen and (max-width: 1000px) {
    .hundred {
      .planet {
        font-size: 60vw;
        line-height: 150vw;
      }
      .vintage-objects {
        height: calc(100vh - 14em) !important;
  
      }
    }
    .honors-wrapper {
      .honors-header {
        font-size: 13vw;
        line-height: 1.2em;
        padding: 0.6em;
      }
    }
  }
