.honor-card {
    padding: 20px 0;
  
    .honors-img {
      max-width: 130px;
      width: 20em;
      padding: 0 1px;
      height: 200px;
  
      img {
        width: 100%;
        object-fit: cover;
        height: 100%;
      }
    }
    .honors-text {
      .honors-label {
        font-size: 1.5em;
      }
      .honors-heading {
        font-size: 2.5em;
        line-height: 1.2em;
      }
      .honors-desc {
        font-size: 1.2em;
        line-height: 1.2em;
        font-family: 'Nunito';
      }
    }
  }
  @media screen and (max-width: 400px) {
    .honor-card {
      .honors-img {
        max-width: 110px;
      }
  
      .honors-text {
        .honors-label {
          font-size: 1.2em;
        }
        .honors-heading {
          font-size: 1.5em;
          line-height: 1.2em;
        }
        .honors-desc {
          font-size: 0.8em;
          line-height: 1.2em;
        }
      }
    }
  }