body {
    overflow-x: hidden;
    max-width: 100vw;
}

.hyperlink-section {
    background-color: #301e03;
}

nav.navbar {
    background-color: #301e03 !important;
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;

    a {
        color: #fff !important;
    }
}

.links-wrapper {
    z-index: 10;
    position: relative;
    .links-handle {
        color: #fff;
    }
}

.hundred2 {
    background-color: #301e03;
    z-index: 1;
}

.link-card {
    border: solid 2px #fff;
    background-color: #fff;
    color: #301e03;

    &:hover {
        background-color: transparent;
        border: 2px solid #fff;
        color: #fff !important;
    }
}