.press-header-desc {
    font-family: 'vnwf-cr', serif;
    color: rgb(223, 169, 20);
}
.press-title {
    font-family: 'EB Garamond', serif;
    line-height: 1em;
}
.press-label {
    border: 2px solid #5d4101;
    color: #5d4101;
    font-family: 'Nunito';
    font-weight: 600;
}
.press-desc {
    font-family: 'Nunito';
}

a.press-link[href] {
    color: #985d03;
    text-decoration: none;
}

a.press-link:hover[href] {
    color: #bb7a18;
    text-decoration: none;
    cursor: url(../img/rightArrow.png), auto;
}